var test = require('ava')

function Stack(capacity) {
    // implement me...
    this.storage = {}
    this.last = undefined
    this.count = 0
    this.capacity = capacity
}

Stack.prototype.push = function(value) {

    if (this.count >= this.capacity) {
        return "Max capacity already reached. Remove element before adding a new one."
    }

    this.storage = Object.assign(this.storage, {
        [this.count] : value,
    })
    this.last = value
    this.count += 1
    return this.count

};
// Time complexity:

Stack.prototype.pop = function() {
    // implement me...
    var oldLast = this.last
    this.count -= 1
    delete this.storage[this.count]
    this.last = this.storage[this.count - 1]
    return oldLast
};
// Time complexity:

Stack.prototype.peek = function() {
    return this.last
};
// Time complexity:

Stack.prototype.count = function() {
    return this.count
};

Stack.prototype.findMe = function  (el) {

    if (!this.last) {
        return false
    }

    for (var i = this.count - 1; i >= 0; i-- ) {
        if (this.storage[i] == el) {
            return true
        }
    }
    return false

}

test.beforeEach(t => {
    t.context.s0 = new Stack(0)
    t.context.s1 = new Stack(3)
    t.context.s1.push('one')
    t.context.s1.push('two')
    t.context.s1.push('four')
    t.context.s2 = new Stack(5)
})

test('method findMe to check if value is in stack', t => {

    var {s0, s1, s2} = t.context

    var act0 = s0.findMe('two')
    var exp0 = false

    var act = s1.findMe('two')
    var exp = true

    t.is(act, exp)

    var act2 = s1.findMe('NotThere')
    var exp2 = false

    t.is(act2, exp2)
})

Stack.prototype.until = function until (val) {
    
    if (this.count === 0 ) {
        return false
    }

    return 3
}

test.only('number of pops until get certain value', t => {

    var {s0, s1, s2} = t.context
    var act = s1.until('one')
    var exp = 3

    t.is(act, exp)

    var act1 = s0.until('one')
    var exp1 = false

    t.is(act1, exp1)

    var act2 = s1.until('four')
    var exp2 = 1

    t.is(act2, exp2)


})
